@import '../../scss';

:host {
  display: block;
  @include apply-themes();

  header.limited-width {
    @include pds-media-query-min('xxl') {
      padding: 0 calc(50vw - 5120px/2);
    }
  }

  header {
    @include pds-grid;

    font-family: $pds-font-family;
    font-weight: $pds-font-weight-regular;
    color: $pds-theme-light-background-base;
    height: $phn-header-height-base;

    @include pds-media-query-min('xs') {
      height: $phn-header-height-xs;
    }

    @include pds-media-query-min('s') {
      height: $phn-header-height-s;
    }

    @include pds-media-query-min('m') {
      height: $phn-header-height-m;
    }

    @include pds-media-query-min('l') {
      height: $phn-header-height-l;
      flex-direction: row;
    }

    .level-0-content {
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      grid-column: wide-start/wide-end;

      .level-0-content-logo {
        display: flex;
        align-items: center;
      }

      .level-0-content-icons {
        position: absolute;
        width: 100%;
      }

      phn-burger-button,
      phn-level-0-icons,
      phn-logo,
      .skeleton-container {
        flex: 1;
        display: flex;
      }

      .skeleton-container.crest-skeleton {
        justify-content: center;
      }

      phn-logo {
        justify-content: center;
        z-index: $phn-z-index-logo-mobile;
        @include bugfix__iOS-transition-zindex-using-transform();

        @include pds-media-query-min('l') {
          z-index: $phn-z-index-logo-mobile;
        }
      }

      .burger-button-skeleton {
        width: p-px-to-rem(24px);
        height: p-px-to-rem(24px);
        border-radius: p-px-to-rem(4px);
        background: $pds-theme-light-contrast-low;

        @include pds-media-query-min('xs') {
          width: p-px-to-rem(76px);
          height: p-px-to-rem(24px);
        }
      }

      .label-bone {
        width: p-px-to-rem(124px);
        height: p-px-to-rem(24px);
        border-radius: p-px-to-rem(4px);
        background-color: $pds-theme-light-contrast-low;

        @include pds-media-query-min('s') {
          width: p-px-to-rem(288px);
        }
      }

