    phn-crest {
      z-index: $phn-z-index-logo-mobile;
      @include bugfix__iOS-transition-zindex-using-transform();

      @include pds-media-query-min('l') {
        z-index: $phn-z-index-logo-mobile;
      }
    }

    phn-burger-button {
      padding: p-px-to-rem(20px) 0 p-px-to-rem(16px) p-px-to-rem(12px);

      @include pds-media-query-min('xs') {
        padding: p-px-to-rem(20px) 0 p-px-to-rem(16px) p-px-to-rem(12px);
      }

      @include pds-media-query-min('s') {
        padding: p-px-to-rem(28px) 0 p-px-to-rem(24px) p-px-to-rem(20px);
      }

      @include pds-media-query-min('m') {
        padding: p-px-to-rem(32px) 0 p-px-to-rem(28px) p-px-to-rem(24px);
      }

      @include pds-media-query-min('xl') {
        padding: p-px-to-rem(36px) 0 p-px-to-rem(32px) p-px-to-rem(28px);
      }
    }

    .burger-button:focus {
      @include design-system__focus();
    }
  }

  .main-content {
    width: p-px-to-rem(20px);
  }

  .main-content:focus {
    outline-style: none;
  }

  .sr-only {
    position: absolute;
    width: p-px-to-rem(1px);
    height: p-px-to-rem(1px);
    white-space: nowrap;
    overflow: hidden;
    clip: rect(1px, 1px, 1px, 1px);
    clip-path: inset(50%);
  }

  phn-p-icon {
    width: p-px-to-rem(24px);

    @include pds-media-query-min('s') {
      width: p-px-to-rem(24px);
    }

    @include pds-media-query-min('m') {
      width: p-px-to-rem(24px);
    }
  }
}
